import { useEffect, useState } from "react";

import Logo from "../assets/logo.svg";


import "../style.css";
import "../styles/header.css";
import "../styles/toggle.css";

import ImgPerfil from "../assets/perfil.png";

export default function Header() {
  const [isDarkMode, setIsDarkMode] = useState(() => false);
  function setTheme(themeName) {
    localStorage.setItem("theme", themeName);
    document.documentElement.className = themeName;
  }
  const [togClass, setTogClass] = useState("dark");
  let theme = localStorage.getItem("theme");

  useEffect(() => {
    if (localStorage.getItem("theme") === "theme-dark") {
      setTogClass("dark");
    } else if (localStorage.getItem("theme") === "theme-light") {
      setTogClass("light");
    }
  }, [theme]);

  useEffect(() => {
    const itens = document.querySelectorAll("nav-item");

    for (let i = 0; i < itens.length; i++) {
      itens[i].onClick = function () {
        let j = 0;
        while (j < itens.length) {
          itens[j++].className = "nav-item";
        }
        itens[i].className = "nav-item active-item";
      };
    }
  });

  const handleOnClick = () => {
    if (localStorage.getItem("theme") === "theme-dark") {
      setTheme("theme-light");
      setTogClass("light");
    } else {
      setTheme("theme-dark");
      setTogClass("dark");
    }
    console.log(theme);
  };

  return (
    <aside id="header" className={theme}>
      <section className="profile">
        <img src={ImgPerfil} alt="Foto do Bruno" />
        <h1>Bruno Micalli</h1>
        <div className="social-links mt-4  text-center">
          <a target="_blank" href="https://www.linkedin.com/in/brunomicalli/">
            <i class="bi bi-linkedin"></i>
          </a>
          <a target="_blank" href="https://github.com/Micalli">
            <i class="bi bi-github"></i>
          </a>
          <a target="_blank" href="https://www.instagram.com/brunomicalli/">
            <i class="bi bi-instagram"></i>
          </a>
        </div>
      </section>

      <nav id="navbar" className="nav-menu">
        <ul class="nav flex-column ">
          <li class="nav-item inicio  ">
            <b></b>
            <b></b>
            <a class="nav-link " aria-current="page" href="#inicio">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="25"
                fill="currentColor"
                class="bi bi-house"
                viewBox="0 0 16 16"
              >
                <path
                  fill-rule="evenodd"
                  d="M2 13.5V7h1v6.5a.5.5 0 0 0 .5.5h9a.5.5 0 0 0 .5-.5V7h1v6.5a1.5 1.5 0 0 1-1.5 1.5h-9A1.5 1.5 0 0 1 2 13.5zm11-11V6l-2-2V2.5a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5z"
                />
                <path
                  fill-rule="evenodd"
                  d="M7.293 1.5a1 1 0 0 1 1.414 0l6.647 6.646a.5.5 0 0 1-.708.708L8 2.207 1.354 8.854a.5.5 0 1 1-.708-.708L7.293 1.5z"
                />
              </svg>
              Início
            </a>
          </li>
          <li class="nav-item sobre ">
          <p className="separator1"></p>

            <b></b>

            <a class="nav-link" href="#about">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="25"
                fill="currentColor"
                class="bi bi-person"
                viewBox="0 0 16 16"
              >
                <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H3s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C11.516 10.68 10.289 10 8 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z" />
              </svg>
              Sobre
            </a>
          </li>
          <li class="nav-item ">
          <p className="separator1"></p>
          <p className="separator2"></p>
            <a class="nav-link habilidade" href="#ability">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="25"
                fill="currentColor"
                className="bi bi-list-check"
                viewBox="0 0 16 16"
              >
                <path
                  fill-rule="evenodd"
                  d="M5 11.5a.5.5 0 0 1 .5-.5h9a.5.5 0 0 1 0 1h-9a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h9a.5.5 0 0 1 0 1h-9a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h9a.5.5 0 0 1 0 1h-9a.5.5 0 0 1-.5-.5zM3.854 2.146a.5.5 0 0 1 0 .708l-1.5 1.5a.5.5 0 0 1-.708 0l-.5-.5a.5.5 0 1 1 .708-.708L2 3.293l1.146-1.147a.5.5 0 0 1 .708 0zm0 4a.5.5 0 0 1 0 .708l-1.5 1.5a.5.5 0 0 1-.708 0l-.5-.5a.5.5 0 1 1 .708-.708L2 7.293l1.146-1.147a.5.5 0 0 1 .708 0zm0 4a.5.5 0 0 1 0 .708l-1.5 1.5a.5.5 0 0 1-.708 0l-.5-.5a.5.5 0 0 1 .708-.708l.146.147 1.146-1.147a.5.5 0 0 1 .708 0z"
                />
              </svg>
              Habilidade
            </a>
          </li> 
          <li class="nav-item ">
          <p className="separator2"></p>
          <p className="separator3  "></p>
            <a class="nav-link curriculo" href="#curriculo">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="25"
                fill="currentColor"
                class="bi bi-file-earmark"
                viewBox="0 0 16 16"
              >
                <path d="M14 4.5V14a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2a2 2 0 0 1 2-2h5.5L14 4.5zm-3 0A1.5 1.5 0 0 1 9.5 3V1H4a1 1 0 0 0-1 1v12a1 1 0 0 0 1 1h8a1 1 0 0 0 1-1V4.5h-2z" />
              </svg>
              Curriculo
            </a>
          </li>
          <li class="nav-item ">
          <p className="separator3  "></p>
          <p className="separator4  "></p>
            <a class="nav-link" href="#portifolio">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="25"
                fill="currentColor"
                class="bi bi-collection"
                viewBox="0 0 16 16"
              >
                <path d="M2.5 3.5a.5.5 0 0 1 0-1h11a.5.5 0 0 1 0 1h-11zm2-2a.5.5 0 0 1 0-1h7a.5.5 0 0 1 0 1h-7zM0 13a1.5 1.5 0 0 0 1.5 1.5h13A1.5 1.5 0 0 0 16 13V6a1.5 1.5 0 0 0-1.5-1.5h-13A1.5 1.5 0 0 0 0 6v7zm1.5.5A.5.5 0 0 1 1 13V6a.5.5 0 0 1 .5-.5h13a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-.5.5h-13z" />
              </svg>
              Portifílio
            </a>
          </li>
          <li class="nav-item ">
          <p className="separator4  "></p>
          <p className="separator5  "></p>
            <a class="nav-link" href="#contato">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="25"
                fill="currentColor"
                class="bi bi-envelope"
                viewBox="0 0 16 16"
              >
                <path d="M0 4a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v8a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V4zm2-1a1 1 0 0 0-1 1v.217l7 4.2 7-4.2V4a1 1 0 0 0-1-1H2zm13 2.383-4.758 2.855L15 11.114v-5.73zm-.034 6.878L9.271 8.82 8 9.583 6.728 8.82l-5.694 3.44A1 1 0 0 0 2 13h12a1 1 0 0 0 .966-.739zM1 11.114l4.758-2.876L1 5.383v5.73z" />
              </svg>
              Contato
            </a>
          </li>
          
        </ul>
        <div className="container--toggle">
          {togClass === "light" ? (
            <input
              type="checkbox"
              id="toggle"
              className="toggle--checkbox"
              onClick={handleOnClick}
              checked
            />
          ) : (
            <input
              type="checkbox"
              id="toggle"
              className="toggle--checkbox"
              onClick={handleOnClick}
            />
          )}
          <label htmlFor="toggle" className="toggle--label">
            <span className="toggle--label-background"></span>
          </label>
        </div>
        <img className="logo" src={Logo} alt="Logo" />
      </nav>
    </aside>
  );
}
